
body{
  margin: 0;
}
.submenu{
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  width: 30%;
  padding: 15px;
  background: linear-gradient(262.87deg,#8159f4 0,#9a76ff 99.25%);

}
.name{
  margin-top: 15px;
  margin-bottom: 10px;
  text-align: center;
  font-size: 28px;
  font-weight: 800;
  
}
.position{
  text-align: center;
  font-size: 18px;
  font-weight: 700;
}

.App{
  display: flex;
  flex-direction: row;
  font-family: sans-serif;
  background-color: #1f1f1fde;
  min-height: 100vh;
}
.submenu-img{
border-radius: 50%;
width: 100%;
object-fit:cover;
object-position:center;
}
.main-block{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 70%;
}
menu{
  width: 100%;
  /* background: linear-gradient(262.87deg,#8159f4 0,#9a76ff 99.25%);  */
  display: flex;
  align-items: center;
  justify-content: center;
  padding:  15px 0;
  margin: 25px 0 45px;
  border-radius: 12px;
  /* padding-top:10px ; */
  /* padding-bottom:10px ; */
}
menu a:hover{
  color: rgba(239, 253, 255, 0.737);
}

menu a{
  text-decoration: none;
  color: #fff;
  font-weight: 700;
  font-size: 18px;
  animation: 300ms ease;
  background-color: rgba(255,255,255,.09);
  border-radius: 6px;
  padding:  15px ;
  /* height: 45px;
  display: flex;
  align-items: center;
  justify-content: center; */



}
menu a.active{
  /* font-weight: 900; */
  text-decoration:underline;
}
.menu-container{
 
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 600px;
  
}